import React, { createContext, useContext, useReducer, useEffect, useMemo } from 'react';
import apiService from '../services/api';
import { useApiData } from '../hooks/useApi';

// Initial state
const initialState = {
  companies: [],
  bugReports: [],
  currentUser: null,
  dashboardStats: null,
  loading: {
    companies: false,
    bugReports: false,
    user: false,
    stats: false,
  },
  error: {
    companies: null,
    bugReports: null,
    user: null,
    stats: null,
  },
};

// Action types
const ActionTypes = {
  SET_LOADING: 'SET_LOADING',
  SET_ERROR: 'SET_ERROR',
  SET_COMPANIES: 'SET_COMPANIES',
  SET_BUG_REPORTS: 'SET_BUG_REPORTS',
  SET_CURRENT_USER: 'SET_CURRENT_USER',
  SET_DASHBOARD_STATS: 'SET_DASHBOARD_STATS',
  ADD_BUG_REPORT: 'ADD_BUG_REPORT',
  UPDATE_BUG_REPORT: 'UPDATE_BUG_REPORT',
  DELETE_BUG_REPORT: 'DELETE_BUG_REPORT',
  ADD_COMPANY: 'ADD_COMPANY',
  UPDATE_COMPANY: 'UPDATE_COMPANY',
  DELETE_COMPANY: 'DELETE_COMPANY',
  CLEAR_ERROR: 'CLEAR_ERROR',
};

// Reducer
const dataReducer = (state, action) => {
  switch (action.type) {
    case ActionTypes.SET_LOADING:
      return {
        ...state,
        loading: {
          ...state.loading,
          [action.payload.key]: action.payload.value,
        },
      };

    case ActionTypes.SET_ERROR:
      return {
        ...state,
        error: {
          ...state.error,
          [action.payload.key]: action.payload.value,
        },
      };

    case ActionTypes.SET_COMPANIES:
      return {
        ...state,
        companies: action.payload,
        loading: { ...state.loading, companies: false },
        error: { ...state.error, companies: null },
      };

    case ActionTypes.SET_BUG_REPORTS:
      return {
        ...state,
        bugReports: action.payload,
        loading: { ...state.loading, bugReports: false },
        error: { ...state.error, bugReports: null },
      };

    case ActionTypes.SET_CURRENT_USER:
      return {
        ...state,
        currentUser: action.payload,
        loading: { ...state.loading, user: false },
        error: { ...state.error, user: null },
      };

    case ActionTypes.SET_DASHBOARD_STATS:
      return {
        ...state,
        dashboardStats: action.payload,
        loading: { ...state.loading, stats: false },
        error: { ...state.error, stats: null },
      };

    case ActionTypes.ADD_BUG_REPORT:
      return {
        ...state,
        bugReports: [...state.bugReports, action.payload],
      };

    case ActionTypes.UPDATE_BUG_REPORT:
      return {
        ...state,
        bugReports: state.bugReports.map(report =>
          report.id === action.payload.id ? action.payload : report
        ),
      };

    case ActionTypes.DELETE_BUG_REPORT:
      return {
        ...state,
        bugReports: state.bugReports.filter(report => report.id !== action.payload),
      };

    case ActionTypes.ADD_COMPANY:
      return {
        ...state,
        companies: [...state.companies, action.payload],
      };

    case ActionTypes.UPDATE_COMPANY:
      return {
        ...state,
        companies: state.companies.map(company =>
          company.id === action.payload.id ? action.payload : company
        ),
      };

    case ActionTypes.DELETE_COMPANY:
      return {
        ...state,
        companies: state.companies.filter(company => company.id !== action.payload),
      };

    case ActionTypes.CLEAR_ERROR:
      return {
        ...state,
        error: {
          ...state.error,
          [action.payload]: null,
        },
      };

    default:
      return state;
  }
};

// Context
const DataContext = createContext();

// Provider component
export const DataProvider = ({ children }) => {
  const [state, dispatch] = useReducer(dataReducer, initialState);

  // Action creators - wrapped in useMemo to prevent infinite re-renders
  const actions = useMemo(() => ({
    setLoading: (key, value) => 
      dispatch({ type: ActionTypes.SET_LOADING, payload: { key, value } }),
    
    setError: (key, value) => 
      dispatch({ type: ActionTypes.SET_ERROR, payload: { key, value } }),
    
    setCompanies: (companies) => 
      dispatch({ type: ActionTypes.SET_COMPANIES, payload: companies }),
    
    setBugReports: (reports) => 
      dispatch({ type: ActionTypes.SET_BUG_REPORTS, payload: reports }),
    
    setCurrentUser: (user) => 
      dispatch({ type: ActionTypes.SET_CURRENT_USER, payload: user }),
    
    setDashboardStats: (stats) => 
      dispatch({ type: ActionTypes.SET_DASHBOARD_STATS, payload: stats }),
    
    addBugReport: (report) => 
      dispatch({ type: ActionTypes.ADD_BUG_REPORT, payload: report }),
    
    updateBugReport: (report) => 
      dispatch({ type: ActionTypes.UPDATE_BUG_REPORT, payload: report }),
    
    deleteBugReport: (reportId) => 
      dispatch({ type: ActionTypes.DELETE_BUG_REPORT, payload: reportId }),
    
    addCompany: (company) => 
      dispatch({ type: ActionTypes.ADD_COMPANY, payload: company }),
    
    updateCompany: (company) => 
      dispatch({ type: ActionTypes.UPDATE_COMPANY, payload: company }),
    
    deleteCompany: (companyId) => 
      dispatch({ type: ActionTypes.DELETE_COMPANY, payload: companyId }),
    
    clearError: (key) => 
      dispatch({ type: ActionTypes.CLEAR_ERROR, payload: key }),

    // API methods
    fetchCompanies: async () => {
      actions.setLoading('companies', true);
      try {
        const companies = await apiService.getCompanies();
        actions.setCompanies(companies);
        return companies;
      } catch (error) {
        actions.setError('companies', error.message);
        throw error;
      }
    },

    fetchBugReports: async (filters = {}) => {
      actions.setLoading('bugReports', true);
      try {
        const reports = await apiService.getBugReports(filters);
        actions.setBugReports(reports);
        return reports;
      } catch (error) {
        actions.setError('bugReports', error.message);
        throw error;
      }
    },

    fetchCurrentUser: async () => {
      actions.setLoading('user', true);
      try {
        const user = await apiService.getCurrentUser();
        actions.setCurrentUser(user);
        return user;
      } catch (error) {
        actions.setError('user', error.message);
        throw error;
      }
    },

    fetchDashboardStats: async () => {
      actions.setLoading('stats', true);
      try {
        const stats = await apiService.getDashboardStats();
        actions.setDashboardStats(stats);
        return stats;
      } catch (error) {
        actions.setError('stats', error.message);
        throw error;
      }
    },

    createBugReport: async (reportData) => {
      try {
        const newReport = await apiService.createBugReport(reportData);
        actions.addBugReport(newReport);
        return newReport;
      } catch (error) {
        throw error;
      }
    },

    updateBugReport: async (reportId, reportData) => {
      try {
        const updatedReport = await apiService.updateBugReport(reportId, reportData);
        actions.updateBugReport(updatedReport);
        return updatedReport;
      } catch (error) {
        throw error;
      }
    },

    deleteBugReport: async (reportId) => {
      try {
        await apiService.deleteBugReport(reportId);
        actions.deleteBugReport(reportId);
      } catch (error) {
        throw error;
      }
    },

    createCompany: async (companyData) => {
      try {
        const newCompany = await apiService.createCompany(companyData);
        actions.addCompany(newCompany);
        return newCompany;
      } catch (error) {
        throw error;
      }
    },

    updateCompany: async (companyId, companyData) => {
      try {
        const updatedCompany = await apiService.updateCompany(companyId, companyData);
        actions.updateCompany(updatedCompany);
        return updatedCompany;
      } catch (error) {
        throw error;
      }
    },

    deleteCompany: async (companyId) => {
      try {
        await apiService.deleteCompany(companyId);
        actions.deleteCompany(companyId);
      } catch (error) {
        throw error;
      }
    },

    searchCompanies: async (query) => {
      try {
        return await apiService.searchCompanies(query);
      } catch (error) {
        throw error;
      }
    },

    searchReports: async (query) => {
      try {
        return await apiService.searchReports(query);
      } catch (error) {
        throw error;
      }
    },
  }), [dispatch]);

  // Load initial data when component mounts
  useEffect(() => {
    // Fetch data for demo purposes (remove auth requirement)
    actions.fetchCompanies();
    actions.fetchBugReports();
    actions.fetchDashboardStats();
    
    // Only fetch user if we have an auth token
    const token = localStorage.getItem('authToken');
    if (token) {
      actions.fetchCurrentUser();
    }
  }, [actions]);

  const value = {
    ...state,
    ...actions,
  };

  return (
    <DataContext.Provider value={value}>
      {children}
    </DataContext.Provider>
  );
};

// Custom hook to use the data context
export const useData = () => {
  const context = useContext(DataContext);
  if (!context) {
    throw new Error('useData must be used within a DataProvider');
  }
  return context;
};
