// API service for connecting to BugBridge Go API
const API_BASE_URL = process.env.REACT_APP_API_URL || (process.env.NODE_ENV === 'production' ? 'http://localhost:55812/api' : '/api');

class ApiService {
  constructor() {
    this.baseURL = API_BASE_URL;
  }

  // Generic request method
  async request(endpoint, options = {}) {
    const url = `${this.baseURL}${endpoint}`;
    const config = {
      headers: {
        'Content-Type': 'application/json',
        ...options.headers,
      },
      ...options,
    };

    // Add auth token if available
    const token = localStorage.getItem('authToken');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }

    try {
      const response = await fetch(url, config);
      
      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(errorData.message || `HTTP error! status: ${response.status}`);
      }

      return await response.json();
    } catch (error) {
      console.error(`API request failed: ${endpoint}`, error);
      throw error;
    }
  }

  // Authentication endpoints
  async login(email, password) {
    return this.request('/auth/login', {
      method: 'POST',
      body: JSON.stringify({ email, password }),
    });
  }

  async register(userData) {
    return this.request('/auth/register', {
      method: 'POST',
      body: JSON.stringify(userData),
    });
  }

  async logout() {
    return this.request('/auth/logout', {
      method: 'POST',
    });
  }

  // User endpoints
  async getCurrentUser() {
    return this.request('/users/me');
  }

  async updateUser(userId, userData) {
    return this.request(`/users/${userId}`, {
      method: 'PUT',
      body: JSON.stringify(userData),
    });
  }

  // Company/Project endpoints
  async getCompanies() {
    return this.request('/companies');
  }

  async getCompany(companyId) {
    return this.request(`/companies/${companyId}`);
  }

  async createCompany(companyData) {
    return this.request('/companies', {
      method: 'POST',
      body: JSON.stringify(companyData),
    });
  }

  async updateCompany(companyId, companyData) {
    return this.request(`/companies/${companyId}`, {
      method: 'PUT',
      body: JSON.stringify(companyData),
    });
  }

  async deleteCompany(companyId) {
    return this.request(`/companies/${companyId}`, {
      method: 'DELETE',
    });
  }

  // Bug Report endpoints
  async getBugReports(filters = {}) {
    const queryParams = new URLSearchParams(filters).toString();
    const endpoint = queryParams ? `/reports?${queryParams}` : '/reports';
    return this.request(endpoint);
  }

  async getBugReport(reportId) {
    return this.request(`/reports/${reportId}`);
  }

  async createBugReport(reportData) {
    return this.request('/reports', {
      method: 'POST',
      body: JSON.stringify(reportData),
    });
  }

  async updateBugReport(reportId, reportData) {
    return this.request(`/reports/${reportId}`, {
      method: 'PUT',
      body: JSON.stringify(reportData),
    });
  }

  async deleteBugReport(reportId) {
    return this.request(`/reports/${reportId}`, {
      method: 'DELETE',
    });
  }

  // Comments endpoints
  async getComments(reportId) {
    return this.request(`/reports/${reportId}/comments`);
  }

  async createComment(reportId, commentData) {
    return this.request(`/reports/${reportId}/comments`, {
      method: 'POST',
      body: JSON.stringify(commentData),
    });
  }

  async updateComment(commentId, commentData) {
    return this.request(`/comments/${commentId}`, {
      method: 'PUT',
      body: JSON.stringify(commentData),
    });
  }

  async deleteComment(commentId) {
    return this.request(`/comments/${commentId}`, {
      method: 'DELETE',
    });
  }

  // Search endpoints
  async searchCompanies(query) {
    return this.request(`/companies/search?q=${encodeURIComponent(query)}`);
  }

  async searchReports(query) {
    return this.request(`/reports/search?q=${encodeURIComponent(query)}`);
  }

  // Statistics endpoints
  async getDashboardStats() {
    return this.request('/stats/dashboard');
  }

  async getCompanyStats(companyId) {
    return this.request(`/stats/company/${companyId}`);
  }

  // File upload endpoints
  async uploadFile(file, reportId) {
    const formData = new FormData();
    formData.append('file', file);
    formData.append('reportId', reportId);

    return this.request(`/reports/${reportId}/attachments`, {
      method: 'POST',
      headers: {
        // Don't set Content-Type for FormData, let browser set it
      },
      body: formData,
    });
  }

  async deleteAttachment(reportId, attachmentId) {
    return this.request(`/reports/${reportId}/attachments/${attachmentId}`, {
      method: 'DELETE',
    });
  }
}

// Create and export a singleton instance
const apiService = new ApiService();
export default apiService;

// Export individual methods for convenience
export const {
  login,
  register,
  logout,
  getCurrentUser,
  updateUser,
  getCompanies,
  getCompany,
  createCompany,
  updateCompany,
  deleteCompany,
  getBugReports,
  getBugReport,
  createBugReport,
  updateBugReport,
  deleteBugReport,
  getComments,
  createComment,
  updateComment,
  deleteComment,
  searchCompanies,
  searchReports,
  getDashboardStats,
  getCompanyStats,
  uploadFile,
  deleteAttachment,
} = apiService;
